<template>
  <view class="category-nav">
    <view class="category-item" v-for="category in categories" :key="category.id" @click="handleClick(category)">
      <image :src="category.icon" mode="aspectFit"></image>
      <text>{{ category.name }}</text>
    </view>
  </view>
</template>

<script>
export default {
  props: {
    categories: {
      type: Array,
      required: true
    }
  },
  methods: {
    handleClick(category) {
      this.$emit('categoryClick', category);
    }
  }
};
</script>

<style>
.category-nav {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.category-item {
  text-align: center;
}

.category-item image {
  width: 50px;
  height: 50px;
}

.category-item text {
  display: block;
  margin-top: 5px;
}
</style>
